type EventType = 
  | HTMLAnchorElement
  | HTMLDivElement;

/**
 *      .
 *       ,
 *        -
 *  .
 * @param {React.MouseEvent<T>} e
 * @param {string} buttonClickedId
 * @param {() => any} action
 * @return {undefined}
 */

export const DroppedMenu = (
  e: React.MouseEvent<EventType>,
  buttonClickedId: string,
  action: ( payload: string ) => any ) => {
    e.preventDefault();
    e.stopPropagation();

    /**
     *    .
     *   Store     
     */
    const remove = () => {
      document.removeEventListener('click', remove);
      action('');
    };
    
    /*      */
    const current: string = 
      String(e.currentTarget.getAttribute('data-button-id'));

    if ( buttonClickedId === '' ) {
      
      /**
       *  Store  ,   .
       *      
       * document   remove(),   
       *  .   Store   
       * data-button-id  .
       */
      document.addEventListener('click', remove);
      action(current);
    } else {
      /*  Store     */
      if ( current === buttonClickedId ) {
        /**
         *  Store       .
         *   Store  .    
         *      document
         */
        action('');
      } else {
        /**
         *  Store   ,   .
         *   Store   .
         *  ,     
         *   document'
         */
        e.nativeEvent.stopImmediatePropagation();
        action(current);
      }
    }
  };